.formControl.error textarea,
.formControl.error input {
    border: red 1px solid;
}
.messageError,
.sumError
{
    background-color: #a45b5b;
    margin: 5px;
    padding: 5px;
    font-size: 12px;
    color: white;
}
.btn {
    text-align: center;
}
input, textarea {
    padding: 3px 6px;
    resize: none;
    border: 1px #dbdbdb solid;
    max-width: 300px;
    width: 100%;
}
input[type="checkbox"] {
    width: 10px;
}
.rememberMe {
    margin: 0 0 30px 15px;
    display: flex;
}
.login {
    background-color: #faf7f7;
    border-radius: 5px;
    line-height: 1.7;
    text-align: center;
    width: 330px;
    margin: 20px auto;
    padding: 0 15px 15px;
}


